body {
  font-size: 16px;
  margin-bottom: 100px;
  text-align: justify;
}
*:focus {
  outline: none !important;
}
p {
  font-size: 1.125rem;
}
nav {
  background: #6060ff;
  background: linear-gradient(90deg, #39c2a2 36%, #0f90ff 100%, rgba(0, 0, 0, 0.1343219464) 200%);
}
.nav-item {
  padding: 6px 16px;
}
.nav-item:hover {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}
.nav-item a {
  color: white !important;
}
.section-heading {
  font-size: 4.5rem;
  font-weight: 400;
}
.userExternalLinks {
  color: #212121;
}

.userExternalLinks i {
  font-size: 1.5rem;
  width: 24px;
}
.info span {
  color: #fff;
  background: #222;
  font-size: 14px;
  padding: 0.25rem 1rem;
  border-radius: 3px;
  cursor: pointer;
  margin-right: 0.5rem;
}
.info span:hover {
  background: #39c2a2;
}
.gradient-text {
  background: linear-gradient(90deg, #39c2a2 36%, #0f90ff 100%, rgba(0, 0, 0, 0.1343219464) 200%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
button.rec-arrow-right,
button.rec-arrow-left {
  position: absolute;
  top: -12px;
  width: 30px !important;
  font-size: 12px;
  padding: 0 !important;
  min-width: 50px;
  min-height: 14px !important;
  background: linear-gradient(90deg, #39c2a2 36%, #0f90ff 100%, rgba(0, 0, 0, 0.1343219464) 200%);
  color: white;
}
button.rec-arrow-right {
  right: 15px;
}
button.rec-arrow-left {
  right: 70px;
}
button.rec-arrow-right:disabled,
button.rec-arrow-left:disabled {
  opacity: 0.5;
}
.rec-pagination {
  display: none !important;
}
@media (min-width: 768px) {
  .userExternalLinks:first-child {
    padding-left: 0;
  }
}
